import React, { useState } from 'react';
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import { Button } from '@mui/material';
import TreasureTable from './TreasureTable';
import { calculateItems } from './Calculator'
import { Item } from './Item'
import './TreasureCalcView.css';

export default function TreasureCalcView() {
    const [textFieldValue, setTextFieldValue] = useState('');
    const [items, setItems] = useState<null | Item[]>();

    // Step 2: Update the state on text field change
    const handleTextFieldChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        setTextFieldValue(event.target.value);
    };

    // Step 3: Add an onClick event handler to the button


    const handleClick = () => {
        setItems(null);
        const experiencePoints = parseInt(textFieldValue);
        let calculatedItems = calculateItems(experiencePoints);
        setItems(calculatedItems);
    };

    return (
        <Box
            className='treasure-view'
            component="form"
            sx={{
                '& > :not(style)': { m: 1, width: '35ch' },
            }}
            noValidate
            autoComplete="off"
        >
            <TextField
                id="standard-basic"
                label=" "
                variant="standard"
                value={textFieldValue}
                placeholder='  '
                InputLabelProps={{
                    style: { direction: 'rtl' },
                }}
                onChange={handleTextFieldChange} />
            <br />
            <Button variant="contained" onClick={handleClick}></Button>
            {!items && <p id='description'>               "  13 + 1.      .          ,         .
                 ,   100    ,  110.    10,        ,        .  20     1-19,           .    20,       .       100  ,      720  ,   7  110 (    100     ).
            </p>}
            {items && <TreasureTable items={items} />}
        </Box>
    );
}


